<template>
  <div class="add_cato_mask">
    <div class="add_cato">
      <div class="add_cato_main">
        <span>请输入类别</span><br>
        <input type="text" v-model="new_cato_name">
      </div>
      <button class="close_btn" @click="closeMe">close</button>
      <div class="add_cato_foot">
        <button @click="closeMe" class="btn_warn">取消</button>
        <button @click="addOneCato" class="btn_sure">确定</button>
      </div>
    </div>
  </div>
</template>

<script>
import { nanoid } from 'nanoid'

export default {
  name: 'AddCato',
  emits: ['toggleShow'],
  data() {
    return {
      new_cato_name: ''
    }
  },
  methods: {
    closeMe() {
      this.$emit('toggleShow', false)
    },
    addOneCato() {
      if (this.new_cato_name === '') {
        return
      }
      const nCato = {
        id: nanoid(),
        name: this.new_cato_name,
        bookmarks: []
      }
      this.$store.commit('ADD_CATO', nCato)
      // 将数据存到localstorage
      localStorage.setItem('bm', JSON.stringify(this.$store.state.bm))
      this.closeMe()
    }
  }
}
</script>

<style scoped>
.add_cato_mask {
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.add_cato {
  width: 30vw;
  height: 20vh;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0 0 0.8em 0px rgb(193 193 193);
  border-radius: 0.4em;

  position: fixed;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.add_cato .add_cato_main {
  padding-left: 0.5em;
}
.add_cato .add_cato_main span {
  font-size: 0.8rem;
}
.add_cato .add_cato_main input {
  width: 76%;
  margin-left: 12%;
  margin-top: 1em;
  height: 40%;
}
.add_cato .add_cato_foot {
  display: flex;
  justify-content: flex-end;
}
.add_cato .add_cato_foot button {
  padding: 0.5em 0.8em;
  margin-right: 5%;
  outline: none;
  cursor: pointer;
}
.btn_sure {
  color: rgb(255, 255, 255);
  border: 0;
  background-color: #eb854f;
}
.btn_sure:hover {
  background-color: #f59d6e;
}
.btn_warn {
  color: #000;
  background-color: rgb(255, 255, 255);
  border: 1px solid #ccc;
}
.btn_warn:hover {
  background-color: rgb(247, 213, 194);
}
.close_btn {
  position: absolute;
  z-index: 1;
  top: 0.3em;
  right: 0.3em;
  cursor: pointer;
  /* 按钮是个小框 */
  width: 2em;
  height: 2em;
  border: 0;
  background-color: #fff;
  /* 把按钮里面的字挤出去并隐藏 */
  text-indent: 10em;
  overflow: hidden;
}
.close_btn::after {
  position: absolute;
  z-index: 1;
  line-height: 0.5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-indent: 0;
  font-size: 2rem;
  content: '\00D7';
}
</style>
