<template>
  <section class="search-body">
    <div class="wrapper-top">
      <div class="search-eng">
        <div class="tip" :data-state="cur_engine_idx">
          <img :src="cur_selected_engine.logourl">
        </div>
        <div class="list">
          <button
            class="choice"
            :data-index="engine.idx"
            v-for="engine of engines"
            :key="engine.idx"
            @click="switchToMe(engine.idx)"
          >
            <img :src="engine.logourl">
          </button>
        </div>
      </div>
      <form :action="cur_selected_engine.form_actions" class="oForm" target="_blank">
        <input
          v-model="cur_input"
          :name="cur_selected_engine.form_name"
          type="text"
          class="search-content"
          autocomplete="off"
          required
          autofocus
        >
      </form>
    </div>
    <ul class="keywords">
        <li @click="jumpTo" v-for="(ali,idx) of rec_list" :key="idx">
          {{ali}}
        </li>
    </ul>
  </section>
</template>

<script>
import { jsonp } from 'vue-jsonp'
export default {
  name: 'SearchBody',
  data() {
    return {
      cur_input: '',
      cur_engine_idx: 0,
      engines: [
        {
          idx: 0,
          name: 'baidu',
          logourl: '/imgs/engines/baidu.png',
          form_actions: 'https://www.baidu.com/s',
          form_name: 'wd'
        },
        {
          idx: 1,
          name: 'bing',
          logourl: '/imgs/engines/bing.png',
          form_actions: 'https://cn.bing.com/search',
          form_name: 'q'
        },
        {
          idx: 2,
          name: 'google',
          logourl: '/imgs/engines/google.png',
          form_actions: 'https://www.google.com/search',
          form_name: 'q'
        },
        {
          idx: 3,
          name: 'so',
          logourl: '/imgs/engines/360.jpg',
          form_actions: 'https://www.so.com/s',
          form_name: 'q'
        },
        {
          idx: 4,
          name: 'sogou',
          logourl: '/imgs/engines/sogou.jpg',
          form_actions: 'https://www.sogou.com/web',
          form_name: 'query'
        },
      ],
      rec_list: []
    }
  },
  computed: {
    cur_selected_engine() {
      return this.engines.find(engine => engine.idx === this.cur_engine_idx)
    }
  },
  methods: {
    switchToMe(idx) {
      this.cur_engine_idx = idx
    },
    jumpTo(ev) {
      const {form_actions, form_name} = this.cur_selected_engine
      const jumpURL = `${form_actions}?${form_name}=${ev.target.innerText}`
      window.open(jumpURL)
    },
    async sentText(val) {
      if (val === '') {
        this.rec_list = []
        return
      }
      const {s: list} = await jsonp('https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su', {
        callbackQuery: 'cb',
        callbackName: 'lzw',
        wd: val
      }).catch(err => console.log(err.message))
      this.rec_list = list
    },
  },
  watch: {
    ['cur_input'](nval) {
      this.sentText(nval)
    }
  }
}
</script>

<style scoped>
.search-body{
  width: 50vw;
  height: 10vh;
  margin: 20vh auto;
  background-color: #fff;
}
.search-body > .wrapper-top {
  width: 100%;
  height: 100%;
  display: flex;
}
.search-body > .wrapper-top > .search-eng{
  height: 10vh;
  width: 10vh;
  overflow: hidden;
}
.search-body > .wrapper-top > .search-eng:hover {
  overflow: visible;
}
.search-body > .wrapper-top > .search-eng > .tip {
  width: 100%;
  height: 10vh;
  text-align: center;
}
.search-body > .wrapper-top > .search-eng > .tip > img {
  /* 图片居中 */
  width: 80%;
  margin-top: 10%;
}
.search-body > .wrapper-top > .search-eng > .list button {
  display: block;
  width: 100%;
  height: 10vh;
  outline: none;
  border: none;
}
.search-body > .wrapper-top > .search-eng > .list button > img {
  width: 80%;
}
/* 搜索框 */
.search-body > .wrapper-top > .oForm{
  flex-grow: 1;
}
.search-body > .wrapper-top > .oForm input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  /* 去掉input默认的padding */
  padding: 0;
  text-indent: 0.5em;
  font-size: 1.5rem;
  color: rgb(73, 73, 73);
}

.search-body .keywords {
  background-color: rgba(255, 255, 255, 0.9);
  list-style: none;
  margin-left: 5vh;
  padding: 0;
}
.search-body .keywords li {
  height: 6vh;
  line-height: 6vh;
  color: rgb(114, 112, 112);
  text-indent: 5vh;
  cursor: pointer;
}
.search-body .keywords li:hover {
  color: rgb(83, 80, 80);
  background-color: antiquewhite;
}
</style>
