import { createRouter, createWebHashHistory } from 'vue-router'
import SearchBody from '../views/SearchBody.vue'
import BookMarksView from '../views/BookMarks.vue'

const routes = [
  {
    path: '/search',
    component: SearchBody
  },
  {
    path: '/',
    redirect: '/search'
  },
  {
    path: '/bm',
    component: BookMarksView,
    redirect: '/bm/bmdetails',
    children: [
      {
        path: 'bmdetails',
        name: 'bmdetails',
        component: () => import('../views/BookMarkBoard.vue')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
