<template>
  <div class=" ">
    <ul class="bmbar">
      <li
        v-for="(cato, idx) of catosList"
        :key="cato.id"
      >
      <!-- :class="idx === cur_cato_idx ? 'active_cato' : ''" -->
        <button class="close_btn" v-show="idx === cur_cato_idx" @click="removeCato(cato.id)">close</button>
        <router-link
          :to="{
            name: 'bmdetails',
            query: {
              id: cato.id
            }
          }"
          @click="switchTab(idx)"
        >
          {{ cato.name }}
        </router-link>
      </li>
      <div class="add_cato_logo" @click="toggleShow(true)">
        <img src="https://s1.ax1x.com/2022/07/18/joIsDf.png">
      </div>
    </ul>
    
    <router-view></router-view>

    <Transition name="CShow">
      <AddCato v-show="showAddCatoFlag" @toggleShow="toggleShow" />
    </Transition>
  </div>
</template>

<script>
import AddCato from '../components/AddCato'
export default {
  name: 'BookMarks',
  emits: ['toggleShow'],
  data() {
    return {
      cur_cato_idx: 0,
      // catosList: this.$store.getters.gotCatos,
      showAddCatoFlag: false
    }
  },
  computed: {
    catosList() {
      return this.$store.getters.gotCatos
    },
  },
  mounted() {
    // console.log(this.$store.getters.gotCatos)
    // 如果localStorage没有，那么转为数字就是NaN，就是假值
    // 高亮太麻烦，取消掉
    // this.cur_cato_idx = Number.parseInt(localStorage.getItem('bar_seq_bookmark')) ?? 0
  },
  methods: {
    switchTab(idx) {
      this.cur_cato_idx = idx
      // localStorage.setItem('bar_seq_bookmark', this.cur_cato_idx)
    },
    toggleShow(tobeShown) {
      this.showAddCatoFlag = tobeShown
    },
    removeCato(catoId) {
      if (window.confirm('确定要删除这个书签分类和所有此类别的书签吗？')) {
        // console.log('删除的id', catoId)
        this.$store.commit('RM_CATO', catoId)
        localStorage.setItem('bm', JSON.stringify(this.$store.state.bm))
        this.$router.push({
          path: '/'
        })
      }
    }
  },
  components: {
    AddCato,
  }
}
</script>

<style scoped>
.CShow-enter-active {
  animation: popon 0.2s ease-out;
}
.CShow-leave-active {
  animation: popon 0.2s reverse ease-in;
}
@keyframes popon {
  from {
    transform: translateY(-5%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.active_cato {
  background-color: rgba(221, 232, 241, 0.6);
}
.bmbar {
  width: 70vw;
  height: 10vh;
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: flex;
}
.bmbar li {
  width: 10vw;
  height: 10vh;
  line-height: 10vh;
  text-align: center;
  display: block;
  transition: 0.5s ease;
  position: relative;
}
.bmbar li:hover{
  background-color: rgba(221, 232, 241, 0.6);
}
.bmbar li > a {
  text-decoration: none;
  color: black;
}
.add_cato_logo {
  width: 10vw;
  height: 10vh;
  line-height: 10vh;
  text-align: center;
}
.add_cato_logo:hover{
  background-color: rgba(221, 232, 241, 0.6);
  cursor: pointer;
}
.add_cato_logo img {
  width: 30%;
  vertical-align: middle;
}
.close_btn {
  position: absolute;
  z-index: 1;
  top: 0.3em;
  right: 0.3em;
  cursor: pointer;
  /* 按钮是个小框 */
  width: 1em;
  height: 1em;
  border: 0;
  /* 把按钮里面的字挤出去并隐藏 */
  text-indent: 10em;
  overflow: hidden;
}
.close_btn::after {
  position: absolute;
  z-index: 1;
  line-height: 0.5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-indent: 0;
  content: '\00D7';
}
</style>
