import { createStore } from 'vuex'

 import bm from './bookmarks'

export default createStore({
  state: {
    bm
  },
  getters: {
    gotCatos(state) {
      if (state.bm.length === 0) {
        return []
      }
      return state.bm.map(cato => {
        return {
          id: cato.id,
          name: cato.name
        }
      })
    },
  },
  mutations: {
    INIT(state, bmFromLocal) {
      state.bm = bmFromLocal
    },
    ADD_CATO(state, catoinfo) {
      state.bm.push(catoinfo)
      // console.log(state.bm, catoinfo)
    },
    ADD_BM(state, bminfo) {
      const idx = state.bm.findIndex(cato => cato.id === bminfo.catoid)
      // console.log('新添加的', bminfo)
      // console.log('找到的', state.bm[idx])
      state.bm[idx].bookmarks.push(bminfo.bmItem)
    },
    RM_CATO(state, catoId) {
      if (state.bm.length === 0) {
        return
      }
      const newbm = state.bm.filter(item => item.id !== catoId)
      state.bm = newbm
    },
    RM_BM(state, {catoId, bmId}) {
      if (state.bm.length === 0) {
        return
      }
      // console.log('bmid', catoId, bmId)
      const targetBMIdx = state.bm.findIndex(cato => cato.id === catoId)
      // console.log('aa', targetBM.bookmarks.filter(bm => bm.id !== bmId))
      state.bm[targetBMIdx].bookmarks = state.bm[targetBMIdx].bookmarks.filter(bm => bm.id !== bmId)
    }
  },
  actions: {
    gotBMsById(ctx, id) {
      if (ctx.state.bm.length === 0) {
        return
      }
      return ctx.state.bm.find(cato => cato.id === id)
    },
    gotBMs(ctx) {
      if (ctx.state.bm.length === 0) {
        return
      }
      return ctx.state.bm[0]
    }
  },
  modules: {
  }
})
